import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const MenuContainer = styled.div`
	width: 216px;
  position: fixed;
  top: 98px;
  margin-left: -16px;
  z-index: 1;
	display:flex;
	flex-direction:column;
	a{
		width: 232px;
    height: 51px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    padding: 0 16px;
    font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    font-size: 16px;
    font-weight: 400;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: -.4px;
    color: #787d86;
    cursor: pointer;
    flex-shrink: 0;
		text-decoration:none;
		&:active{
			color: #00bdb8;
    	font-weight: 700;
		}
		&:hover{
			background-color: #f6f8fa;
		}
	}
`;

function Navigation() {
  return (
    <MenuContainer>
      <Link
				to='/home'
			>For You</Link>
      <Link
				to='/myhighlights/highlights'
			>My Highlights</Link>
      <Link
				to="/more"
			>More</Link>
    </MenuContainer>
  );
}

export default Navigation;